import React from 'react'

class LifecycleChild extends React.PureComponent{
    constructor(props){
        super(props);
        this.state={

            salary:60000
        }
        console.log("I am from Constructor : child PureComponent");

    }

    componentDidMount(){
        console.log("I am from Componentdidmount : child PureComponent");
    }


    callstate=()=>{

        this.setState({
            salary:this.state.salary+0
        })
    }

    componentDidUpdate(){
        console.log("i am from componentDid Update..: child PureComponent");

    }

    render(){
        console.log("I am from render : : child PureComponent");
        return(
            <div>
                <h1>Pure Comonent ..Child. sal {this.state.salary}</h1>
                <button onClick={this.callstate}>Update State: child</button>

            </div>
        )
    }

}

export default LifecycleChild;