import React, { Component } from 'react'
import ANRContext from './UserContext'
import RNStudios from './RamaNaiduContext'

export class SonDetails extends Component {
    constructor(props) {
        super(props)
    
        this.state = {         
        }    
    }
    
    render() {
        return (
            <div>
                <ANRContext.Consumer>
                    {
                        (anrgifts)=>(  
                            <RNStudios.Consumer>
                                {
                                      (rnvalue)=>(
                                           <div>
                                               <h3>RN Gigt : {rnvalue}</h3>
                                              <h3>ANR Gift {anrgifts[1].gift}</h3>

                                          </div>
                                    )
                                }
                            </RNStudios.Consumer>                
                        )
                    }
                </ANRContext.Consumer>
            </div>
        )
    }
}

export default SonDetails


