import React, { Component } from 'react'

export class RefChild extends Component {
    constructor(props) {
        super(props)
    
        this.state = {
             salary:60000
        }

        this.nameref=React.createRef();
    }

    drawmoneyusingref=()=>{

        this.setState({
            salary:this.state.salary-2500
        })

    }


    focusinput=()=>{
        this.nameref.current.focus();
    }

    
    render() {
        return (
            <div style={{backgroundColor:'green'}}>
                <h3>this is from Child</h3>
                <input type='test' ref={this.nameref}></input>
                <h3>avaliable sal : {this.state.salary}</h3>
                <button onClick={this.drawmoneyusingref}>Draw Money</button>
                <button onClick={this.focusinput}>Focus Input</button>

                <h3>this is from Child</h3>
            </div>
        )
    }
}

export default RefChild
