import React from 'react'

class Refexp extends React.Component{
    constructor(props){
        super(props);
        this.state={

        }
        this.usernameref=React.createRef();
        this.ageref=React.createRef();
    }

    submitform=(event)=>{
        console.log(this.usernameref);

        alert(this.usernameref.current.value +"Age :"+this.ageref.current.value);
        event.preventDefault();

    }


    componentDidMount(){
        this.ageref.current.focus();
    }

    render(){
        return(
            <div>
                <h1>Welcome to Refs</h1>
                <form onSubmit={this.submitform}>
                <div>
                    <label>User Name</label>
                    <input type='text' ref={this.usernameref}></input>

                </div>
                <div>
                    <label>Age</label>
                    <input type='number'  ref={this.ageref}></input>
                </div>
                  <input type='submit'></input>
                </form>
                

            </div>
        )
    }

}

export default Refexp;