import React, { Component } from 'react'
import Nagarjuna from './Father'


export class GrandFather extends Component {
    constructor(props) {
        super(props)
    
        this.state = {
             anrassets:'50c'
        }
    }
    
    render() {
        return (
            <div>
                <h3>Grand Father : ANR</h3>
                <Nagarjuna assets={this.state.anrassets}></Nagarjuna>
            </div>
        )
    }
}

export default GrandFather
