import React, { Component } from 'react'
import CChild from './CChild'

export class CParent extends Component {
    constructor(props) {
        super(props)
    
        this.state = {
             isEnabeld:true
        }
    }

    changeState =()=>{
        this.setState({
            isEnabeld:!this.state.isEnabeld
        })
    }
    
    render() {
        return (
            <div style={{backgroundColor:'green'}}>
                <button onClick={this.changeState}>Toggle</button>
                <h3>i am in Parent Comp</h3>
                {this.state.isEnabeld && <CChild></CChild>}
                <h3>i am in Parent Comp</h3>
                
            </div>
        )
    }
}

export default CParent
