import React from 'react'

class UserDetails extends React.Component{
    constructor(props){
        super(props);
        this.state={
            name:'',
            userdesciption:'',
            location:'',
            attended:false
        }
    }

    handlename=(event)=>{
        this.setState({
            name:event.target.value
        })
    }


    handledesc=(event)=>{
        this.setState({

userdesciption:event.target.value
        })
    }

    handleloaction=(event)=>{
        this.setState({
            location:event.target.value
        })

    }


    formsubmit=(event)=>{
        alert("Nmae : "+this.state.name+" Des : "+this.state.userdesciption+" Location : "+this.state.location +" attended ?/ : "+this.state.attended);

           event.preventDefault();
    }


    handleallinputs=(event)=>{

        const valuedet=event.target.name==='attended'?event.target.checked:event.target.value

        this.setState({
            [event.target.name]:valuedet
            
        })

    }


    render(){
        return(
            <form onSubmit={this.formsubmit}>
                <div>
                    <label>User Name</label>
                    <input id='name'  name='name' value={this.state.name} type='text' onChange={this.handleallinputs}></input>
                    <div>
                        <label>Descition</label>
                        <textarea type='text' name='userdesciption' id='userdec' value={this.state.userdesciption} onChange={this.handleallinputs}></textarea>
                    </div>
                    <div>
                        <label>Location</label>
                        <select name='location' value={this.state.location} onChange={this.handleallinputs} >
                            <option value='HYd'>HYd</option>
                            <option value='Bang'>Bang</option>
                            <option value='Chennai'>Chennai</option>
                            <option value='Mumnai'>Mumnai</option>
                        </select>
                           <label>selected city : {this.state.location}</label>
                    </div>
                    <div>
                        <label>Attened</label>
                        <input type='checkbox' name='attended' value={this.state.attended} onChange={this.handleallinputs}></input>
                    </div>
                </div>
               <input type='submit' value='Submit'></input>
            </form>
        )
    }

}

export default UserDetails;