import React, { Component } from 'react'
import axios from 'axios'

export class RestGet extends Component {
    constructor(props) {
        super(props)
    
        this.state = {
             posts:[]
        }

        this.useridRef=React.createRef();
        this.titleRef=React.createRef();
        this.bodyRef=React.createRef();
    }

    componentDidMount(){

        axios.get('https://jsonplaceholder.typicode.com/posts?userId=1').
        then(response=>{
            console.log(response);
            this.setState({
                posts:response.data
            })
        }).
        catch(error=>{
            console.log(error);
        })

    }

    postmydata=()=>{

        let myinfo={
            "userId": this.useridRef.current.value,
            "title": this.titleRef.current.value,
            "body": this.bodyRef.current.value
          }

        axios.post('https://jsonplaceholder.typicode.com/posts',myinfo).
        then(response=>{
            console.log(response);

            this.state.posts.push(response.data);
            this.forceUpdate();
        

        }).
        catch(error=>{
            console.log(error);
        })

    }
    
    render() {

        const postdata=this.state.posts.map(post=><h3 key={post.id}>ID :{post.userId} Title {post.title} </h3>)

        return (
            <div>
                <h1>Rest Api Calls</h1>
                <div>
                    <div>
                        <label>user Id</label>
                        <input type='text' ref={this.useridRef}></input>
                    </div>
                    <div>
                        <label>Title</label>
                        <input type='text' ref={this.titleRef}></input>
                    </div>
                    <div>
                        <label>Body</label>
                        <input type='text' ref={this.bodyRef}></input>
                    </div>
                </div>
                <button onClick={this.postmydata}>Post Data</button>
                <div>
                    {postdata}
                </div>   
            </div>
        )
    }
}

export default RestGet
