import React, { Component } from 'react'
import Columns from './Columns'
import Player from './Player';

export class FragmentExp extends Component {

    constructor(props) {
        super(props)
    
        this.state = {
             players:[
                 {name:'Federer',titles:'20'},
                 {name:'Nadal',titles:'19'},
                 {name:'Djokovic',titles:'17'},
                 {name:'Sampras',titles:'14'}
                ]
        }
    }
    

    render() {

        let playdet=this.state.players.map(player=>
        <React.Fragment key={player.name}>
                    <tr>
                     <td>{player.name}</td>
                     <td>{player.titles}</td>
                       </tr>
      </React.Fragment>);

        return (
            <>
                <h1>Grand Slam Titiles</h1>
                <table>
                    <thead>
                    <tr>
                        <Columns></Columns>
                    </tr>
                    </thead>
                    <tbody>
                       {playdet}
                    </tbody>
                    
                </table>
            </>
        )
    }
}

export default FragmentExp
