import React from 'react'

class Krishna extends React.Component{
    constructor(props){
        super(props);
        this.state={
            salary:60000
        }
    }

    render(){
        return(
            <div>
           <h1>I am {this.props.name}</h1>
            <h1> i have {this.props.assets}</h1>
            <h1>my salary : {this.state.salary}</h1>
        </div>
        )
    }

}

export default Krishna;

