import React , {Component, PureComponent} from 'react'
import LifecycleChild from './LifecycleChild'
import RegComponent from './RegComponent'

class Lifecycle extends Component{
    constructor(props){
        super(props);
        this.state={
            salary:60000,
            bonus:0,
            rolno:16789
        }
        console.log("I am from Constructor");

        this.inputref=React.createRef();

    }

    componentDidMount(){
        console.log("I am from Componentdidmount");
    }

    static getDerivedStateFromProps(props, state){
        console.log("i am in getDerivedStateFromProps");

        state.bonus=props.variablebonus;

        return null
    }

    shouldComponentUpdate(newProps,newState){
        console.log(" i am in shouldComponentUpdate");
        // console.log(newProps);
        // console.log(newState);

        if(newState.salary<80000){
          return false
        }
        else{
            return true
        }
     // return true
    }


    callstate=()=>{

        this.setState({
            salary:this.state.salary+10000
        })
    }

    getSnapshotBeforeUpdate(prevProps, prevState){
        console.log("i am in getSnapshotBeforeUpdate");
        return true;
    }

    componentDidUpdate(prevProps, prevState,snapshot){
        console.log("i am from componentDid Update..");
        console.log(snapshot);
       // this.inputref.current.disabled=snapshot;
    }

    render(){
        console.log("I am from render");
        return(
            <div>
                <h3>Bonus : {this.state.bonus}</h3>
                {/* <input type='text' ref={this.inputref}></input> */}
                <h1>Life Cycle methods ... {this.state.salary}</h1>
                <button onClick={this.callstate}>Increse Slaraly</button>
            </div>
        )
    }

}

export default Lifecycle;