import React from 'react'

class Daugther extends React.Component{
    constructor(props){
        super(props);
        this.state={
           
            bike:'TVS Jupitor'

        }
    }

    render(){

        const {name,atm}=this.props

        const {salary,bike}=this.state

        return(
            <div>
                <h1> Name :- {name}</h1>
                <h1>My bike {bike}</h1>
                <button onClick={()=>atm(5000)}>Draw Money from Dads Account</button>

            </div>
        )
    }

}

export default Daugther;