import React ,{useState,useEffect} from 'react'

function HookEffects() {

    const [count,setCount]=useState(0);

    const [salary,updateSalary]=useState(50000);

    const [designation,updateDesignation]=useState('SE');

    useEffect(()=>{
        console.log('i am not happy  : '+count)
    },[])


    useEffect(()=>{
        console.log("i am very happy : "+salary)
        console.log('my Designation : '+designation)
    },[salary,designation])


    return (
        <div>
            <button onClick={()=>setCount(prevCount=>prevCount+1)}> Team Count {count}</button>
            <button onClick={()=>updateSalary(salary+10000)}>Salary {salary}</button>
            <button onClick={()=>updateDesignation('SSE')}>Des : {designation}</button>
        </div>
    )
}

export default HookEffects
