import React from 'react'
import Son from './Son'
import Daugther from './Daugther'

class Dad extends React.Component{
    constructor(props){
        super(props);
        this.state={
            money:500000,
            assets:"10 Acrs",
            plots:2
        }
    }

    drawmoney=(amount)=>{
       const {money,assets,plots}=this.state
        this.setState(()=>({
            money:money-amount
        }),()=>{})
    }

    render(){
        return(
            <div>
                <h1>Family details</h1>
                <h1>Fathers Money : {this.state.money}</h1>
                <Son totstate={this.state}></Son>
                <Daugther name="sarayu" fathersmoney={this.state.money} assets={this.state.assets} atm={()=>this.drawmoney()}></Daugther>
                <button onClick={()=>this.drawmoney(5000)}>Draw Money</button>

            </div>
        )
    }

}

export default Dad;