import React, { Component } from 'react'

export class ChildCom extends Component {
constructor(props) {
    super(props)

    this.state = {
        
    }

    this.childRef=React.createRef();
}

focusinput=()=>{
    this.childRef.current.focus();
}


    render() {
        return (
            <div>
               
                <input ref={this.childRef}></input>
                <button onClick={this.focusinput}> Child : Focus Input</button>
            </div>
        )
    }
}

export default ChildCom
