import React, { Component } from 'react'
import NagaChaitanya from './SonDetails'

export class Father extends Component {
    constructor(props) {
        super(props)
    
        this.state = {
             nagassets:'100c'
        }
    }
    
    render() {
        return (
            <div>
                <h3>Father : Nagarjuna fathers assets {this.props.assets}</h3>
                <NagaChaitanya assets={this.state.nagassets} grandfassets={this.props.assets}></NagaChaitanya>
            </div>
        )
    }
}

export default Father
