import React, { Component } from 'react'
import {Super30} from './Super30'

export class HoverCount extends Component {

    render() {
        return (
            <div>
                <h3>from super 30 ..hover..</h3>
                <h1 onMouseOver={this.props.incrementCount}>Hover Count : {this.props.count}</h1>
            </div>
        )
    }
}

export default Super30(HoverCount,2)
