import React from 'react'
import Player from './Player'

class IndianCricket extends React.Component{
    constructor(props){
        super(props);
        this.state={
            scoreboard:[
                {name:'K L Rahul',runs:1},
                {name:'Rohit Sharma',runs:1},
                {name:'Virat Kohli',runs:1},
                {name:'Pant',runs:32},
                {name:'Dinesh Karthik',runs:6},
                {name:'Pandya',runs:32},
                {name:'Dhoni',runs:50},
                {name:'Jadeja',runs:77},
                {name:'Dhoni',runs:50}

            ]

        }


    }

    render(){
        const {scoreboard}=this.state

       let indiascoreboard=scoreboard.map((player,index)=><Player key={index} name={player.name} runs={player.runs}></Player>)

        return(
            <div>
                  {indiascoreboard}
            </div>
        )
    }

}

export default IndianCricket;