import React from 'react'

class Son extends React.Component{
    constructor(props){
        super(props);
        this.state={
            salary:70000,
            bike:'Royal Enfield Thunderbird'

        }

    }

    render(){

        const {totstate}=this.props
        const {salary,bike}=this.state
        
        return(
            <div>
                <h1> Name :- {totstate.name} Money : {totstate.fathersmoney}  assets : {totstate.assets}</h1>
        <h1>salary {salary} , My bike {bike}</h1>

            </div>
        )
    }

}

export default Son;