import React from 'react'

class Employee extends React.Component{
    constructor(props){
        super(props);
        this.state={
            salary:60000
        }
    }

    

    increment=()=>{

        this.setState((prevState)=>({salary:prevState.salary+10000}),()=>{console.log(this.state.salary)})
    }

    incrementbyfive=()=>{
        this.increment();
        this.increment();
        this.increment();
        this.increment();
        this.increment();

    }


    render(){
        return(
            <div>
                <h1>Emp Id : {this.props.empid} Location : {this.props.location}</h1>
        <h1>Salary : {this.state.salary}</h1>
                <button onClick={()=>this.incrementbyfive()}>Increment</button>
            </div>
        )
    }



}

export default Employee;