import React from 'react'

function SonR(props) {
    const {marks,updatemarks}=props
    return (
        <div>
           <button onClick={()=>updatemarks([...marks,marks[2].marks=888])}>Update Name</button>
           <div>
            {
                marks.map(item=><h1>{item.marks}</h1>)
            }
           </div>
           
        </div>
    )
}

export default SonR
