import React from 'react'

export const Super30=(OrdinoryStudent,incby)=>{

    class SuperStudent extends React.Component{
        constructor(props) {
            super(props)
        
            this.state = {
                 count:0
            }
        }
        
        incrementCount=()=>{
            this.setState((prevState)=>({
                count:prevState.count+incby
            }))
        }
    
        render(){
            return(
                <OrdinoryStudent count={this.state.count} incrementCount={this.incrementCount}></OrdinoryStudent>
            )
        }

    }

    return SuperStudent;

}

