import React, { Component } from 'react'

export class DauR extends Component {
    constructor(props) {
        super(props)
    
        this.state = {
             salary:50000
        }
    }
    
    render() {

        const {money,drawmoney}=this.props

        return (
            <div>
                <button onClick={()=>drawmoney(10000)}> Dau : Draw {money}</button>
            </div>
        )
    }
}

export default DauR
