import React from 'react'

class RegComponent extends React.Component{
    constructor(props){
        super(props);
        this.state={

            salary:60000
        }
        console.log("I am from Constructor  : child RegComponent");

    }

    componentDidMount(){
        console.log("I am from Componentdidmount : child RegComponent");
    }


    callstate=()=>{

        this.setState({
            salary:this.state.salary+0
        })
    }

    componentDidUpdate(){
        console.log("i am from componentDid Update..: child RegComponent");

    }

    render(){
        console.log("I am from render : : child RegComponent");
        return(
            <div>
                <h1>Reg Comp Cycle methods ..Child. sal {this.state.salary}</h1>
                <button onClick={this.callstate}>Update State: child</button>

            </div>
        )
    }

}

export default RegComponent;