import React, { Component } from 'react'
import DauR from './DauR'
import SonR from './SonR'

export class ParentR extends Component {
    constructor(props) {
        super(props)
    
        this.state = {
             
            money:0,
            name:{firstName:'Viat',lastName:'Kohli jkjk'},
            marks:[{id:'1',marks:'500'},{id:'2',marks:'550'},{id:'3',marks:'489'}]
        }
    }

    updatemarks=(newvalue)=>{
        this.setState({
            marks:newvalue
        })
    }


    
    render() {
        return (
            <div>
                {/* <h3>Aval Money : {this.state.money}</h3> */}

                {/* <button onClick={()=>this.drawmoney(5000)}>Draw money</button> */}
               
                <SonR marks={this.state.marks} updatemarks={this.updatemarks}></SonR>
            </div>
        )
    }
}

export default ParentR

 {/* <DauR money={this.state.money} drawmoney={this.drawmoney}></DauR> */}
